.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
  position: relative;
}

.card__product {
  text-align: center;
  box-sizing: border-box;
  display: block;
}

.card__product-img {
  flex-shrink: 0;
  width: 160px;
  height: 134px;
  margin-bottom: 36px;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
}

.card__reviews {
  flex-shrink: 0;
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-review {
  color: #060b35;
  text-align: right;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: flex-end;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

body {
  box-sizing: border-box;
  flex-direction: column;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

p, a, h2 {
  margin: 0;
}

.stars {
  justify-content: space-between;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars.stars--1 > .stars__star:first-child, .stars.stars--2 > .stars__star:nth-child(-n+2), .stars.stars--3 > .stars__star:nth-child(-n+3), .stars.stars--4 > .stars__star:nth-child(-n+4), .stars.stars--5 > .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo__img {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  margin: auto 50px;
  display: block;
}

.nav {
  margin: 0 50px 0 0;
}

.nav__list {
  flex-direction: row;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  white-space: nowrap;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: auto 10px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
  transition-duration: .3s;
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

